import React, { useEffect, useState } from 'react';
import './Result.css';
import { TEST_RESULTS_DATA } from '../TestData';
import { Link } from 'react-router-dom';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import imgApply from '../assets/apply.png';
import no_human_tel_master from '../assets/result/no_human_tel_master.jpg';
import craft_master from '../assets/result/craft_master.jpg';
import top_star_manager from '../assets/result/top_star_manager.jpg';
import school_trip_devil from '../assets/result/school_trip_devil.jpg';
import a_lot_of_money_sales_man from '../assets/result/a_lot_of_money_sales_man.jpg';
import last_scene_writer from '../assets/result/last_scene_writer.jpg';
import turkey_icecream_man from '../assets/result/turkey_icecream_man.jpg';
import two_time_naturer from '../assets/result/two_time_naturer.jpg';
import god_of_progress from '../assets/result/god_of_progress.jpg';
import million_youtuber from '../assets/result/million_youtuber.jpg';

const Result = (state) => {
    const curruntUrl = window.location.href;
    const curruntId = state?.match?.params?.id;
    const result = TEST_RESULTS_DATA[curruntId];
    let mainImg = '';

    if(!result) {
        state.history.push({pathname: `/`});
    }

    switch (curruntId) {
        case 'no_human_tel_master':
            mainImg = no_human_tel_master;
            break;
        case 'craft_master':
            mainImg = craft_master;
            break;
        case 'top_star_manager':
            mainImg = top_star_manager;
            break;
        case 'school_trip_devil':
            mainImg = school_trip_devil;
            break;
        case 'a_lot_of_money_sales_man':
            mainImg = a_lot_of_money_sales_man;
            break;
        case 'last_scene_writer':
            mainImg = last_scene_writer;
            break;
        case 'turkey_icecream_man':
            mainImg = turkey_icecream_man;
            break;
        case 'two_time_naturer':
            mainImg = two_time_naturer;
            break;
        case 'god_of_progress':
            mainImg = god_of_progress;
            break;
        case 'million_youtuber':
            mainImg = million_youtuber;
            break;
    }
    
    const mouseOverEvent = {
        onMouseEnter: (e) => e.target.style.transform = 'scale(1.1)',
        onPointerEnter: (e) => e.target.style.transform = 'scale(1.1)',
        onMouseLeave: (e) => e.target.style.transform = 'scale(1.0)',
        onPointerLeave: (e) => e.target.style.transform = 'scale(1.0)',
    };

    const onCopy = () => {
        alert('URL이 복사되었습니다.');
    }

    const onClickKaKao = () => {
        if(!window.Kakao.isInitialized()) {
            window.Kakao.init('6d7e8dba0ec3559c2f6742198b230e4e');
        }

        window.Kakao.Link.sendDefault({
            objectType: 'feed',
            content: {
                title: '재미있는 심리테스트',
                description: '재미있는 심리테스트를 해봅시다!',
                imageUrl: 'https://lee-yumin.github.io/react-survey-public/survey.png',
                link: {
                    mobileWebUrl: 'https://lee-yumin.github.io/react-survey-public/',
                    webUrl: 'https://lee-yumin.github.io/react-survey-public/'
                }
            }
        });
    };

    return (
        <div id="result-main-container">
            <hr id="header-bar"/>
            <div id="content-container">
                <div id="main-text">{result['name']}</div>
                <div id="logo-container">
                    <img id="logo" src={mainImg} alt="logo" />
                </div>
                <div id="sub-text">{result['descript'].join('\n\n')}</div>
            </div>

            <div id="share-container">
                <div id="share-text">친구에게 공유하기</div>
                <div id="div-share-button1">
                    <CopyToClipboard onCopy={onCopy} text={window.location.href} {...mouseOverEvent}>
                        <button id="btn-share-url" className="url-copy-btn share-button" />
                    </CopyToClipboard>
                    <button id="btn-share-kakao" className="share-button" onClick={onClickKaKao} {...mouseOverEvent}/>
                    <a href={`http://www.facebook.com/sharer/sharer.php?u=${curruntUrl}`} target="_blank" {...mouseOverEvent}>
                        <button id="btn-share-facebook" className="share-button"/>
                    </a>
                    <a href={`https://twitter.com/intent/tweet?url=${curruntUrl}`} target="_blank" {...mouseOverEvent}>
                        <button id="btn-share-twitter" className="share-button"/>
                    </a>
                </div>

                <div id="div-share-button2">
                    <Link to={{pathname: '/'}}>
                        <button id="btn-restart-test" {...mouseOverEvent} />
                    </Link>
                    <a href="http://www.againschool.imweb.me" target="_blank" {...mouseOverEvent}>
                        <button id="btn-share-againschool" className="share-button"/>
                    </a>
                    <a href="https://twitter.com/intent/tweet?url=https://lee-yumin.github.io/react-survey-public" target="_blank" {...mouseOverEvent}>
                        <button id="btn-share-instargram" className="share-button"/>
                    </a>
                </div>
            </div>

            <div id="apply-container">
                <a href="https://form.office.naver.com/form/responseViewMobile.cmd?formkey=ZDE5MDI4YWItNGNiNi00MmYxLThkZWQtMDg4ZjRmMDU2OTFk" target="_blank">
                    <img id="img-apply" src={imgApply} alt="" />
                </a>
            </div>

            <hr id="footer-bar"/>
        </div>
    )
};

export default Result;